@import './../../styles/index.scss';

.stepsWrapper {
  background-color: $c-header;
  width: 100%;
  height: 1150px;
  background: linear-gradient(to top, black, $c-header, black);

  @media screen and (max-width: $tablet) {
    height: 1600px;
  }
}

.steps {
  padding: 30px;
  margin: 0 auto;

  @media screen and (max-width: $phone) {
    padding: 15px;
  }
}

.content {
  display: flex;
  justify-content: space-evenly;
  gap: 50px;
  align-items: center;

  @media screen and (max-width: $desktop) {
    flex-direction: column;
    justify-content: start;
    gap: 30px;
  }
}


.cardTitle {
  font-size: 22px;
  color: $c-white;

  @media screen and (max-width: $tablet) {
    font-size: 18px;
  }
}

.block {
  background-color: $c-white;
  width: 100%;
  max-width: 260px;
  height: 260px;
  padding: 30px;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  box-shadow: $box-shadow;
  transition: $t-normal;

  &:hover {
    box-shadow: none;
    transform: scale(1.1);
  }
}

.blockTitle {
  font-weight: 600;
  text-align: center;
  font-size: 20px;
  line-height: 26px;
}

.blockTitle:nth-of-type(2) {
  margin-bottom: 10px;
}

.text {
  line-height: 26px;
  text-align: center;
  color: $c-white;

  @media screen and (min-width: $phone) and (max-width: $desktop) {
    text-align: left;
  }

  @media screen and (max-width: $phone) {
    text-align: center;
  }
}

.gifContainer {
  width: 60px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.icon {
  height: 50px;
  margin-top: 10px;
  margin-bottom: 30px;
  text-align: center;
}

.animationContainer {
  width: 70px;
  height: 70px;
}

.animationContainerIdea {
  display: flex;
  align-items: center;
  width: 150px;
  height: 60px;

  @media screen and (max-width: $desktop) {
    flex-basis: 60px;
    flex-grow: 0;
    flex-shrink: 0;
  }
}

.number {
  font-size: 18px;
  color: $c-white;
  font-weight: 700;
}

.titleContainer {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;

  @media screen and (min-width: $phone) and (max-width: $desktop) {
    justify-content: start;
    margin: 0;
  }

  @media screen and (max-width: $phone) {
    justify-content: start;
    margin: 0;
    gap: 10px;
  }
}

.cardContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 330px;
  gap: 30px;

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 100%;
    gap: 60px;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 5px 10px;
  }

  @media screen and (max-width: $tablet) {
    background-color: rgba(255, 255, 255, 0.1);
    max-width: 100%;
    padding: 40px 20px;
    gap: 15px;
  }
}

.arrowDown {
  display: block;
  width: 100px;
  height: 100px;
  margin: 0 auto;
  margin-top: 100px;
  margin-bottom: 100px;
  transition: $t-normal;

  @media screen and (max-width: $phone) {
    width: 60px;
    height: 60px;
    margin-top: 30px;
  }
}

.buttons {
  margin: 0 auto;
  width: 100%;
  max-width: 600px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media screen and (max-width: $phone) {
    flex-direction: column;
    align-items: center;
    gap: 50px;
  }
}

.subtitleSecond {
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  color: $c-red;
  margin-bottom: 20px;
  text-transform: uppercase;

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    font-size: 18px;
    margin-bottom: 50px;
  }

  @media screen and (max-width: $phone) {
    font-size: 16px;
    margin-bottom: 15px;
  }
}

.iconContainer {
  width: 24px;
  height: 24px;

  @media screen and (max-width: $phone) {
    width: 20px;
  }
}

.iconTelegram {
  animation: icon 5s ease-in-out;
}

.link {
  padding: 10px 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  opacity: 0.9;
  color: $c-white;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  border: 1px solid $c-white;
  border-radius: 30px;
  // box-shadow: $box-shadow;
  transition: $t-normal;
}

.link:hover {
  box-shadow: none;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  color: white;
  transform: scale(1.1);
}

.link:active {
  background-color: $c-link-active;
}

.linkContainer {
  cursor: pointer;
}

.linkFirst .linkSecond {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.linkSecond {
  @media screen and (max-width: $phone) {
    font-size: 14px;
  }
}

.buttonContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
margin: 0 auto;  gap: 10px;
}

.tel {
  font-family: $ff-main;
}
